
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import moment from 'moment'

export default {
directives: {},
components: {
XInput,
Datetime,
Group,
Grid,
GridItem
},
name: 'sales-return-list',
data () {
return {
propUser: this.user,

fetchConfig: {
pageIndex: 1,
pageSize: 10,
rowsCount: 0,
keyword: '',
beginDate: '',
endDate: ''
},
orderList: [],
searchList: [],
displayList: []
}
},
created () {
if (!this.user.isLogin) {
this.$router.push('Login')
return
}

this.fetchConfig.beginDate = moment().subtract(6, 'months').format('YYYY-MM-DD')
this.fetchConfig.endDate = moment().format('YYYY-MM-DD')
this.fetchOrderListByConfig()
},
methods: {
fetchOrderListByConfig () {
let postData = { }
postData.userName = this.user.userName
postData.password = this.user.password
postData.fetchConfig = this.fetchConfig
this.$http.post(this.config.Sales.SalesReturn.fetchURL, postData).then(orderRes => {
orderRes = orderRes.data
if (!orderRes.status) {
this.$router.push('Index')
return
}
this.fetchConfig.rowsCount = orderRes.info.rowsCount
this.orderList = orderRes.info.rows
this.searchList = this.orderList
this.displayList = this.orderList
})
},
searchTextChange (val) {
this.fetchConfig.pageIndex = 1
this.fetchOrderListByConfig()
},
filterDataByDate () {
this.fetchConfig.pageIndex = 1
this.fetchOrderListByConfig()
},
handleCurrentPageChange (val) {
this.fetchConfig.pageIndex = val
this.fetchOrderListByConfig()
}
},
computed: {},
props: ['user', 'config']
}



