
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
directives: {
TransferDom
},
components: {
XHeader,
Actionsheet
},
name: 'header',
data () {
return {
propUser: this.user,
header: {
leftOptions: {
showBack: true,
backText: '主页',
preventGoBack: true
},
rightOptions: {
showMore: true
},
showMenus: false,
title: ''
}
}
},
created () {



},
methods: {
headerLeftClick () {
this.$router.push('/Index')
},
headerRightClick () {
this.header.showMenus = true
},




menuLogoutClick () {
console.log('log out')

this.$emit('reset')
this.$router.push('/Home')
},
arrangeTitle () {
switch (this.$route.path) {
case '/Login':
this.header.title = '登录'
break
case '/Index':
this.header.title = '主页'
break
case '/PurchaseOrderQuery':
this.header.title = '查询采购订单'
break
case '/PurchaseReturnQuery':
this.header.title = '查询采购退货单'
break
case '/SalesOrderQuery':
this.header.title = '查询销售订单'
break
case '/SalesQuoteQuery':
this.header.title = '查询销售报价单'
break
case '/SalesReturnQuery':
this.header.title = '查询销售退货单'
break
case '/AllocationListQuery':
this.header.title = '查询调拨单'
break
case '/PurchaseInboundQuery':
this.header.title = '查询采购入库单'
break
case '/SalesOutboundQuery':
this.header.title = '查询销售出库单'
break
case '/OtherInboundQuery':
this.header.title = '查询其他入库单'
break
case '/OtherOutboundQuery':
this.header.title = '查询其他出库单'
break
}
}
},
computed: {
arragneMenus () {
if (this.propUser.isLogin) {
return {
userName: 'UserName: ' + this.propUser.userName,
name: 'Name: ' + this.propUser.name,
email: 'Email: ' + this.propUser.email,
mobile: 'Mobile: ' + this.propUser.mobile,
logout: '退出登录/Logout'
}
} else {
return null
}
}
},
watch: {
$route (to, from) {
this.arrangeTitle()
}
},
props: ['user']
}



