
import Group from 'vux/src/components/group/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'


export default {
name: 'sales-outbound-detail',
directives: {},
components: {
Group,
Grid,
GridItem
},
data () {
return {
orderDetail: {}
}
},
created () {
let orderID = this.$route.params.ID
let postData = {}
postData.userName = this.user.userName
postData.password = this.user.password
postData.fetchConfig = {
id: orderID
}
this.$http.post(this.config.Warehouse.SalesOutbound.detailURL, postData).then(orderRes => {
orderRes = orderRes.data
if (!orderRes.status) {
this.$router.push('Index')
return
}
if (orderRes.info === null) {
this.$router.go(-1)
return
}
this.orderDetail = orderRes.info
})
},

methods: {},
computed: {},
props: ['user', 'config']
}



